import { buttons } from "./data.js";

const generateBtns = (container) => {
  buttons.forEach((button) => {
    const btn = document.createElement("button");
    btn.innerHTML = button;

    button === "Del" && btn.classList.add("btn-delete");
    button === "Clear" && btn.classList.add("btn-clear");
    button === "=" && btn.classList.add("btn-equals");
    button === "Ans" && btn.classList.add("btn-answer");

    container.appendChild(btn);
  });
};

export default generateBtns;
